'use strict';

/**
* Simultaneously compute the sine and cosine of an angle measured in radians on `[-π/4, π/4]` in single-precision floating-point format.
*
* @module @stdlib/math-base-special-kernel-sincosf
*
* @example
* import kernelSincosf from '@stdlib/math-base-special-kernel-sincosf';
*
* var v = kernelSincosf( 0.0 );
* // returns [ ~0.0, ~1.0 ]
*
* @example
* import Float32Array from '@stdlib/array-float32';
* import kernelSincosf from '@stdlib/math-base-special-kernel-sincosf';
*
* var out = new Float32Array( 2 );
*
* var v = kernelSincosf.assign( 0.0, out, 1, 0 );
* // returns <Float32Array>[ ~0.0, ~1.0 ]
*
* var bool = ( v === out );
* // returns true
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import assign from './assign.js';


// MAIN //

setReadOnly( main, 'assign', assign );


// EXPORTS //

export default main;
